import React, { Component } from 'react';
import { Modal }  from 'react-bootstrap';
import { baseUrl } from '../../../../Assets/AssetsHelper';

class SyncProductsModal extends Component {
    constructor(props) {
        super(props);
            this.state = {
        };
    }

    handleClose = () => {
        this.props.handleSyncProCallback(false)
    };

    render() {
        const { syncProModalToggle } = this.props
        return <Modal show={syncProModalToggle} onHide={this.handleClose}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
                >
                    <Modal.Header closeButton>
                        <Modal.Title>Sync Product Catalogue</Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <div className="syncPro_modal mx-xl-4 px-2 px-lg-4 mx-0">
                            <div className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                                <div className="d-flex flex-shrink-0">
                                    <div className="d-flex align-items-center mb-2 py-1">
                                        <span className="symbol symbol-30 me-2">
                                            <img className="img-fluid h-100" src={baseUrl("media/icons/platform-icons/google.svg")} height={43} width={43} alt="twitter" />
                                        </span>
                                        <span className="fw-semibold fs-7 text-ellipsis">Google Merchant</span>
                                    </div>
                                </div>
                                <div className="d-flex flex-grow-1">
                                    <div className="mb-2 w-100 mx-0 mx-sm-3">
                                        <input type="text" className="form-control is-invalid" placeholder="Google Merchant ID" />
                                        <div className="invalid-feedback">Please enter a valid Google Merchant ID</div>
                                    </div>
                                </div>
                                <div className="d-flex flex-shrink-0">
                                    <button type="button" className="btn border-0 d-flex signInWithBrand signIn-Google mb-2">
                                        <span className="h-36px w-36px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1">
                                            <img src={baseUrl("media/icons/platform-icons/google.svg")} height={43} width={43} alt="twitter" />
                                        </span>
                                        <div className="overflow-hidden signIn_txt">
                                            <span className="text-light-alt mb-0 w-100 fs-9 fw-medium text-ellipsis me-2">Sign in with Google</span>
                                        </div>
                                    </button>
                                </div>
                            </div>
                            <div className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                                <div className="d-flex flex-shrink-0">
                                    <div className="d-flex align-items-center mb-2 py-1">
                                        <span className="symbol symbol-30 me-2">
                                            <img className="img-fluid h-100" src={baseUrl("media/icons/platform-icons/shopify.svg")} height={20} width={20} alt="twitter" />
                                        </span>
                                        <span className="fw-semibold fs-7 text-ellipsis">Shopify</span>
                                    </div>
                                </div>
                                <div className="d-flex flex-grow-1">
                                    <div className="mb-2 w-100 mx-0 mx-sm-3">
                                        <input type="text" className="form-control" placeholder='Enter your "myshopify" store URL' />
                                    </div>
                                </div>
                                <div className="d-flex flex-shrink-0">
                                    <button type="button" className="btn border-0 d-flex signInWithBrand signIn-Shopify mb-2">
                                        <span className="h-36px w-36px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1">
                                            <img src={baseUrl("media/icons/platform-icons/shopify.svg")} height={20} width={20} alt="twitter" />
                                        </span>
                                        <div className="overflow-hidden signIn_txt">
                                            <span className="text-light-alt mb-0 w-100 fs-9 fw-medium text-ellipsis me-2">Connect Shop</span>
                                        </div>
                                    </button>
                                </div>
                            </div>
                            <div className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                                <div className="d-flex flex-shrink-0">
                                    <div className="d-flex align-items-center mb-2 py-1">
                                        <span className="symbol symbol-30 me-2">
                                            <img className="img-fluid h-100" src={baseUrl("media/icons/platform-icons/bigcommerce.png")} height={20} width={20} alt="twitter" />
                                        </span>
                                        <span className="fw-semibold fs-7 text-ellipsis">BigCommerce</span>
                                    </div>
                                </div>
                                <div className="d-flex flex-grow-1">
                                    <div className="mb-2 w-100 mx-0 mx-sm-3">
                                        <p className="mb-0 fs-7">Install our BigCommerce app to sync products.</p>
                                    </div>
                                </div>
                                <div className="d-flex flex-shrink-0">
                                    <button type="button" className="btn border-0 d-flex signInWithBrand signIn-Bigcommerce mb-2">
                                        <span className="h-36px w-36px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1">
                                            <img src={baseUrl("media/icons/platform-icons/bigcommerce.png")} height={20} width={20} alt="twitter" />
                                        </span>
                                        <div className="overflow-hidden signIn_txt">
                                            <span className="text-light-alt mb-0 w-100 fs-9 fw-medium text-ellipsis me-2">Install App</span>
                                        </div>
                                    </button>
                                </div>
                            </div>
                            <div className="syncPro_modal_wrapper mb-2 mb-lg-3 mb-xl-4">
                                <div className="d-flex flex-shrink-0">
                                    <div className="d-flex align-items-center mb-2 py-1">
                                        <span className="symbol symbol-30 me-2">
                                            <img className="img-fluid h-100" src={baseUrl("media/icons/platform-icons/magento.svg")} height={20} width={20} alt="twitter" />
                                        </span>
                                        <span className="fw-semibold fs-7 text-ellipsis">Magento</span>
                                    </div>
                                </div>
                                <div className="d-flex flex-grow-1">
                                    <div className="mb-2 w-100 mx-0 mx-sm-3">
                                        <input type="text" className="form-control" placeholder='Enter your "magento" store URL' />
                                    </div>
                                </div>
                                <div className="d-flex flex-shrink-0">
                                    <button type="button" className="btn border-0 d-flex signInWithBrand signIn-Magento mb-2">
                                        <span className="h-36px w-36px d-flex align-items-center flex-shrink-0 signInWithBrandIcon me-1">
                                            <img src={baseUrl("media/icons/platform-icons/magento.svg")} height={20} width={20} alt="twitter" />
                                        </span>
                                        <div className="overflow-hidden signIn_txt">
                                            <span className="text-light-alt mb-0 w-100 fs-9 fw-medium text-ellipsis me-2">Connect Shop</span>
                                        </div>
                                    </button>
                                </div>
                            </div>
                        </div>
                    </Modal.Body>
                </Modal>

    }
}

export default SyncProductsModal;